.wrapper div{
    height: 350px;
    flex-direction: column;
    font-family: 'Source Code Pro', monospace;
    font-weight: 600;

}
.background{
    background-repeat: no-repeat;
    background-size: 100% 100%;
    opacity: 0.6;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 10px;

}
.box{
    overflow: hidden;
    position: relative;
}
.title{
    color: #2f2f2f;
    text-shadow:
            0 0 5px #ffc300,
            0 0 5px #ffc300,
            0 0 10px #ffcb27,
            0 0 20px #ffcb27,
            0 0 40px #ffe283,
            0 0 80px #fff0c1;
    font-family: 'Staatliches', sans-serif;
    font-size: 2.7rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    opacity: 1;
}
.countdown{
    position: relative;
}
.countdown .numbers{
    color: #2f2f2f;
    text-shadow:
            0 0 5px #ffc300,
            0 0 5px #ffc300;
    font-family: 'Staatliches', sans-serif;
    position: absolute;
    font-size: 4rem;
    bottom: 0;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    padding-bottom: 5px;
    opacity: 1;
}
.countdown .days{
    font-size: 1rem;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    padding-bottom: 10px;
    opacity: 1;
}
.deleteBtn{
    box-sizing: border-box;
    z-index: 1;
    color: #ffc300;
    background-color: transparent;
    border: 0;
    width: auto;
    height: auto;
    font-size: 2rem;
    position: absolute;
    margin: 10px;
}
button:hover{
    color: #ff0000;
}